
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

































*{
			font-style: normal;
			padding: 0;
			margin: 0;
			list-style: none;
			-webkit-user-select: none;
}
.xz{
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-ms-flex-wrap: wrap;
			    flex-wrap: wrap;
			/*justify-content: space-between;*/
}
.xz li{
      border: 1px solid #ccc;
      width: 23%;
      margin-right: 2%;
      height: 300px;
      margin-bottom: 20px;
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
}
.xz li:hover{
			border: 1px solid #f66;
}
a{
			font-size: 20px;
}
.title{
			font-size: 20px;
			text-align: center;
}
.xz img{
			display: block;
			width: 100%;
			height: 150px;
}
.text{
			-webkit-box-sizing: border-box;
			        box-sizing: border-box;
			margin-top: 50px;
			font-size: 20px;
			padding: 0px 10px;
			display:-webkit-box;
			display:-ms-flexbox;
			display:flex;
			-webkit-box-pack: justify;
			    -ms-flex-pack: justify;
			        justify-content: space-between;
			-webkit-box-align: center;
			    -ms-flex-align: center;
			        align-items: center;
}
.price{
			color: red;
			font-size: 20px;
}
.carIco{
      background: #00B7FF;
      display: block;
      width: 110px;
      height: 25px;
      font-size: 16px;

      border-radius:7px;
      text-align: center;
      line-height: 25px;
      color: white;
      cursor: pointer;
}







































































































































